import http from '@/utils/http'

export const fetchTypes = () => {
  return http.get('/app-alert/getAlertType')
}

export const fetchRank = () => {
  return http.get('/app-alert/getAlertRank')
}

// 获取最近24小时的告警级别统计
export const fetchLast24hRankCount = () => {
  return http.get('/app-alert/getLast24hAlertRankCount')
}

// 获取最近24小时的告警种类统计
export const fetchLast24hTypeCount = () => {
  return http.get('/app-alert/getLast24hAlertTypeCount');
}

//获取最近7天的告警级别统计
export const fetchLast7DayRankCount = () => {
  return http.get('/app-alert/getLast7DayAlertRankCount')
}
